/* global document console Excel */

/** Test for membership in a type */
export class Util {
  static inEnum<T>(type: T, test: string | number): boolean {
    return Object.values(type).includes(test as T);
  }
}

/** Log entry to DOM element */
export function addToLog(elementId: string, logText: any) {
  const logger = document.getElementById(elementId);
  if (typeof logText == "object") {
    logger.innerHTML += (JSON && JSON.stringify ? JSON.stringify(logText, undefined, 2) : logText) + "<br />";
  } else {
    logger.innerHTML += logText + "<br />";
  }
}

export function showSetupPanel() {
  document.getElementById("setup-panel").style.display = "block";
  document.getElementById("make-panel").style.display = "none";
  document.getElementById("nav__list-setup").className = "nav__link nav-active";
  document.getElementById("nav__list-make").className = "nav__link";
}

export function showMakePanel() {
  document.getElementById("setup-panel").style.display = "none";
  document.getElementById("make-panel").style.display = "flex";
  document.getElementById("nav__list-make").className = "nav__link nav-active";
  document.getElementById("nav__list-setup").className = "nav__link";

  const linkElems = document.querySelectorAll("a.nav__link");
  console.log(linkElems);
  linkElems.forEach((link) => {
    link.className = "nav__link";
  });
}

export function selectNavMenu(navElemId: string, paneltoShowId: string) {
  const linkElems = document.querySelectorAll("a.nav__link");
  console.log(linkElems);
  linkElems.forEach((link) => {
    if (link.id == navElemId) {
      link.className = "nav__link nav-active";
    } else {
      link.className = "nav__link";
    }
  });

  const panelELems = document.querySelectorAll("div.panel");
  panelELems.forEach((panel: HTMLElement) => {
    if (panel.id == paneltoShowId) {
      panel.style.display = "flex";
    } else {
      panel.style.display = "none";
    }
  });
}

