/** Tag group object */
export class TagGroup {
  Name: string;
  readonly Type: number;
  Tags: ITag[];
  static ValidTagTypes: string[] = ["REAL", "SINT", "INT", "DINT", "LINT"];

  constructor(name: string) {
    this.Name = name;
    this.Type = 1;
    this.Tags = new Array();
  }

  /** Add tags to group */
  addNumericTag(name: string, type: string, address: number): Boolean {
    // Check against valid list of numeric types
    if (TagGroup.ValidTagTypes.includes(type) && address >= 0) {
      const newTag: ITag = {
        Name: name,
        Type: type,
        Address: address,
      };
      this.Tags.push(newTag);
      return true;
    } else {
      //throw Error(`Tag ${name} not added - invalid type`);
      return false;
    }
  }

  addStringTag(name: string, address: number, length: number) {
    if (address >= 0 && length > 0) {
      const newTag: ITag = {
        Name: name,
        Type: "STRING",
        Address: address,
        Length: length,
      };
      this.Tags.push(newTag);
      return true;
    } else {
      return false;
    }
  }

  addBoolTag(name: string, address: number, bitAddress: number) {
    if (address >= 0 && bitAddress >= 0 && bitAddress < 8) {
      const newTag: ITag = {
        Name: name,
        Type: "BOOL",
        Address: address,
        BitAddress: bitAddress,
      };
      this.Tags.push(newTag);
      return true;
    } else {
      return false;
    }
  }
}

/** Represents a tag source object (file) */
export class TagSource {
  Name: string;
  TagGroups: TagGroup[];

  constructor(name: string) {
    this.Name = name;
    this.TagGroups = new Array();
  }
}

/** Trigger object */
export interface Trigger {
  Tag: string;
  Type: string;
  Latch: boolean;

  /*   OnChange: When current tag value does not equal last value
  RisingEdge: When current tag value is greater than past value
  FallingEdge: When current tag value is less than past value
  Equal: When current tag value equals a given threshold value
  NotEqual: When current tag value does not equal a given threshold value
  Less: When current tag value is less than a given threshold value
  LessOrEqual: When current tag value is less than or equal to a given threshold value
  Greater: When current tag value is greater than a given threshold value
  GreaterOrEqual: When current tag value is greater than or equal to a given threshold value
 */
}

/** Data channel object */
export class DataChannel {
  Name: string;
  Tag: string;
  Trigger?: Trigger;
  ValueDictionary?: object;

  constructor(name: string, tag: string) {
    this.Name = name;
    this.Tag = tag;
  }
  /** Add value dictionary from object */
  addValueDictionary(valueDictionary: object) {
    // NEED ERROR CHECKING??
    this.ValueDictionary = valueDictionary;
  }

  /** Add self as trigger */
  addTriggerSelf(type: string) {
    const trigger: Trigger = {
      Tag: this.Tag,
      Type: type,
      Latch: false,
    };

    this.Trigger = trigger;
  }
}

/** Represents a data source object (file) */
export class DataSource {
  Name: string;
  UseLegacyDataTypes = false;
  Type: string;
  TagSource: string;
  ServerAddress: string;
  ServerPort: number;
  Database: string;
  Table: string;
  Trigger?: Trigger;
  ShiftSchedule?: object;
  StaticMetadata?: object;
  DataChannels: DataChannel[];
  MetadataChannels: DataChannel[];
  Tags?: string[];

  static ValidDataSourceTypes: string[] = ["Continuous", "TriggeredChannels", "TriggeredSet", "Duration", "FaultStack"];

  constructor(
    name: string,
    sourceType: string,
    tagSource: string,
    serverAddress: string,
    dbName: string,
    table: string
  ) {
    if (DataSource.ValidDataSourceTypes.includes(sourceType)) {
      this.Name = name;
      this.UseLegacyDataTypes = false;
      this.Type = sourceType;
      this.TagSource = tagSource;
      this.ServerAddress = serverAddress;
      this.ServerPort = 65400;
      this.Database = dbName;
      this.Table = table;
      this.DataChannels = new Array();
      this.MetadataChannels = new Array();

      //return true;
    } else {
      //return false;
    }
  }

  /** Add hard-coded shift schedule as default */
  addDefaultShiftData() {
    this.ShiftSchedule = {
      Shifts: [
        {
          Name: "Shift 1",
          Start: "07:00:00",
          End: "15:00:00",
        },
        {
          Name: "Shift 2",
          Start: "15:00:00",
          End: "23:00:00",
        },
        {
          Name: "Shift 3",
          Start: "-01:00:00",
          End: "07:00:00",
        },
      ],
    };
  }
  /** Add shift schedule data from string input
   * @todo NEED ERROR CHECKING
   */
  addShiftSchedule(shiftSchedule: string) {
    this.ShiftSchedule = JSON.parse(shiftSchedule);
  }

  /** Add data channel to object
   * @todo NEED ERROR CHECKING
   * @todo Add triggered channels work
   */
  addDataChannel(name: string, tag: string, valueDict?: object, trigger?: Trigger) {
    const dataChannel = new DataChannel(name, tag);
    valueDict ? dataChannel.addValueDictionary(valueDict) : null; /** @todo Verify code */

    if (this.Type == "TriggeredChannels" && trigger) {
      dataChannel.Trigger = trigger;
    } else if (this.Type == "TriggeredChannels" && !trigger) {
      dataChannel.addTriggerSelf("OnChange");
    }

    this.DataChannels.push(dataChannel);
  }

  /** Add metadata channel to object
   * @todo NEED ERROR CHECKING
   */
  addMetadataChannel(dataChannel: DataChannel) {
    this.MetadataChannels.push(dataChannel);
  }
}
