/**  Create template sheet for Logix tag export */
async function createTagsInSheet(sheetName: string = "TagsIn") {
  await Excel.run(async (context) => {
    // Try to reference the TagsIn sheeet
    let sheet = context.workbook.worksheets.getItemOrNullObject(sheetName);
    await context.sync();

    if (sheet.isNullObject) {
      sheet = context.workbook.worksheets.add(sheetName);

      // Set the report title in the worksheet
      const remCells = sheet.getRange("A1:B6");

      const remData = [
        ["remark", "SHEET FORMATTED TO MATCH TAG EXPORT"],
        ["remark", "Date = "],
        ["remark", "Version = RSLogix 5000 > v32.04"],
        ["remark", "Owner = "],
        ["remark", "Company = "],
        ["0.3", ""],
      ];

      remCells.values = remData;
      remCells.format.autofitColumns();

      const headerCells = sheet.getRange("A7:F7");
      const headerData = [["TYPE", "SCOPE", "NAME", "DESCRIPTION", "DATATYPE", "EIDOS"]];

      headerCells.values = headerData;
      headerCells.format.autofitColumns();
      // headerCells.format.set(PROPS_H2);

      let aColWidths = [60, 100, 180, 200, 100, 30];
      for (let i = 0; i < aColWidths.length; i++) {
        headerCells.getColumn(i).format.columnWidth = aColWidths[i];
      }

      // Populate with sample data
      const sampData = [
        ["TAG", "", "TAG_NAME_1", "Tag Description", "RCP_PV", "Y"],
        ["TAG", "", "TAG_NAME_2", "Tag Description", "REAL", "y"],
      ];

      const sampCells = sheet.getRange("A8:F9");
      sampCells.values = sampData;

      await sheet.context.sync();

      console.log("TagsIn template sheet created");
    } else console.log("WARNING: TagsIn sheet already exists");

    await sheet.context.sync();
  });
}

