// Generate list of unique Data Types and write to the TagsGen sheet
async function getUniqueDTs1(sheetName: string) {
  await Excel.run(async (context) => {
    // Look for existing sheet
    const tagsInSheet = context.workbook.worksheets.getItemOrNullObject(sheetName);
    await context.sync();

    if (tagsInSheet.isNullObject) {
      console.log(`Cannot find ${sheetName} sheet`);
    } else {
      // Select the first Name cell to make sure it isn't empty
      let tagsIn = tagsInSheet.getRange("C8").load(["rowCount", "values"]);
      await context.sync();

      if (tagsIn.values[0][0] != "") {
        // Get range of the entire set of cells (non blanks)
        tagsIn = tagsInSheet.getRange("C8:F8").getExtendedRange("Down");
        tagsIn.select();
        tagsIn.load(["rowCount", "values"]);
        await context.sync();

        // Extract a list of unique Data types that are selected to collect
        let dataTypes = [];
        for (let row of tagsIn.values) {
          let dTypIn = row[2];
          let eidYN = row[3];
          if (dTypIn != "" && eidYN != "" && dataTypes.indexOf(dTypIn) == -1) {
            dataTypes.push(dTypIn);
          }
        }
        console.log(dataTypes);

        // Make sure the array isn't empty
        if (dataTypes.length > 0) {
          // Need to create an array of arrays to put values into Range cells
          let aDtListVals = [];
          dataTypes.forEach((item) => aDtListVals.push([item]));

          // Create sheet if it doesn't exist
          createTagGenSheet();
          // Create destination range ref
          let shTagsGen = context.workbook.worksheets.getItem("TagsGen");
          let rDtDest = shTagsGen.getRangeByIndexes(1, 0, dataTypes.length, 1);

          rDtDest.values = aDtListVals;
          rDtDest.select();
        }
      }
    }
  });
}

// Remove duplicate and blank values from a Range value array
// eslint-disable-next-line @typescript-eslint/no-unused-vars
function removeDupsBlanks(rngArray) {
  let newArray = [];

  for (let row = 0; row < rngArray.length; row++) {
    let valIn = rngArray[row][0];
    if (valIn != "" && newArray.indexOf(valIn) == -1) {
      newArray.push(rngArray[row][0]);
    }
  }
  return newArray;
}

// Get list of DataTypes and build dictionary
async function buildTagList() {
  await Excel.run(async (context) => {
    // BUILD MAP OF DATA TYPES
    const shTagGen = context.workbook.worksheets.getItem("TagsGen");

    // Select all cells in the Data type definition range
    const range = shTagGen.getRange("A2:B2").getExtendedRange("Down").load(["rowCount", "values"]);
    range.select();
    await context.sync();

    // Define map to house the Data types and assoc members
    // and populate map.
    let mDT = new Map();
    for (let row of range.values) {
      mDT.set(row[0], row[1].split(";"));
    }
    // ** TODO: NEED TO ACCOUNT FOR EMPTY NAME ROW(S)
    // CREATE EXPANDED TAG LIST
    const shTagsIn = context.workbook.worksheets.getItem("TagsIn");
    // Select all cells in the Tag list range
    const rTagsIn = shTagsIn.getRange("C8:F8").getExtendedRange("Down").load(["rowCount", "values"]);
    await context.sync();

    // Loop through input range and generate list of tags+members
    let aTagList = [];
    let iCount = 0;
    let iCountMems = 0;

    for (let row of rTagsIn.values) {
      let sEidos = row[3];
      let sTagName = row[0];
      let sTagDT = row[2];

      if (sEidos != "") {
        iCount++;

        if (mDT.has(sTagDT)) {
          let mMemList = mDT.get(sTagDT);
          for (let mems in mMemList) {
            iCountMems++;
            aTagList.push([sTagName + mMemList[mems]]);
          }
        } else {
          iCountMems++;
          aTagList.push([sTagName]);
        }
      }
    }

    if (aTagList.length > 0) {
      // Finally write that list to the TagsGen worksheet
      const rTagsOut = shTagGen.getRangeByIndexes(1, 3, aTagList.length, 1);
      rTagsOut.values = aTagList;
      rTagsOut.select();

      console.log("Processed " + iCount + " tags, " + iCountMems + " members");
    } else console.log("No tags selected");
  });
}

