/* global, Excel, Office */

/**
 * Tag map object to hold definition of column layout and provide helper functions
 */
export const DataSrcRow = {
  columns: [
    { key: "Byte", title: "Byte" },
    { key: "Bit", title: "Bit" },
    { key: "EidosTag", title: "Eidos Tag" },
    { key: "ChannelName", title: "Eidos Chan Name" },
    { key: "DataSource", title: "Data Source" },
    { key: "TagType", title: "Data Type" },
    { key: "Metadata", title: "Metadata" },
    { key: "PlcTagName", title: "PLC Tag" },
    { key: "PlcTagIndex", title: "Index PLC" },
    { key: "PlcTagBit", title: "Bit PLC" },
  ],

  get headers(): string[] {
    let headers = this.columns.map((item) => item.title);
    return headers;
  },

  getColumnIndex(key: string): number {
    const i: number = this.columns.findIndex((item) => item.key == key);
    if (i >= 0) {
      return i;
    } else {
      return null;
    }
  },

  makeObject(row: string[]) {
    if (row.length == this.columns.length) {
      let obj = {};

      row.forEach((item, index) => {
        obj[this.columns[index].key] = item;
      });

      return obj;
    } else {
      return null;
    }
  },

  makeRow(obj: object) {
    const keys = Object.keys(obj);
    let row = new Array(this.columns.length);

    keys.forEach((key) => {
      const i: number = this.columns.findIndex((item) => item.key == key);
      if (i >= 0) {
        row[i] = obj[key];
      }
    });

    return row;
  },
};

/** Table definition parameters */
export type TableDef = {
  Name: string;
  StartCell: { Row: number; Column: number };
  Columns: string[];
  SampleData: string[][];
};

