/** Create Excel table based on definition data */
// eslint-disable-next-line no-undef
export function makeTable(tableDef: TableDef, sheet: Excel.Worksheet, style: string = "TableStyleLight1"): Excel.Table {
  // Create table range object
  const rowLength = tableDef.SampleData.length;
  const colLength = tableDef.Columns.length;

  // Try to add table to sheet
  try {
    const tableRange = sheet.getRangeByIndexes(tableDef.StartCell.Row, tableDef.StartCell.Column, rowLength, colLength);
    let table = sheet.tables.add(tableRange, false);

    // Populate properties
    table.name = tableDef.Name;
    table.style = style;

    // Populate data
    table.getHeaderRowRange().values = [tableDef.Columns];
    table.getDataBodyRange().values = tableDef.SampleData;
    return table;
  } catch (error) {
    // log error
  }
}
